import React, {useState} from 'react';

export default function TextForm(props) {
    const [text, setText] = useState("");

    const handleUpClick = ()=>{
        // console.log("You have clicked Handle Up click Button" + text);
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("! Text has been converted to upper case!!", "success")
    }

    const handleLoClick = ()=>{
        // console.log("You have clicked Handle Up click Button" + text);
        let newText = text.toLowerCase();
        setText(newText);
        props.showAlert("! Text has been converted to lower case!!", "success")
    }

    const handleOnChange = (event)=>{
        // console.log("OnChange");
        setText(event.target.value)
    }

    const handleCopy = ()=>{
        let copyText = document.getElementById('exampleFormControlTextarea1');
        copyText.select();
        navigator.clipboard.writeText(copyText.value);
        props.showAlert("! Text has been Copied!!", "success")
    }

    const handleClearText = ()=>{
        setText('');        
    }

    return (
        <>
        <div className="container mb-3" style = {{color : props.mode === 'dark'?'white':'black'}}>
            <h1>{props.heading}</h1>
            <div className="mb-3">
            <textarea className="form-control my-3" id="exampleFormControlTextarea1" onChange={handleOnChange} value={text} style = {{backgroundColor : props.mode === 'dark'?'#6a679b':'white', color : props.mode === 'dark'?'white':'black'}} rows="8"></textarea>
            </div>
            <button className="btn btn-success mx-2 my-1" onClick={handleUpClick} >Convert to Upper Case</button>
            <button className="btn btn-success mx-2 my-1" onClick={handleLoClick} >Convert to Lower Case</button>
            <button className="btn btn-success mx-2 my-1" onClick={handleCopy} >Copy Text</button>
            <button className="btn btn-success mx-2 my-1" onClick={handleClearText} >Clear Text</button>
        </div>
        <div className="container mb-3" style = {{color : props.mode === 'dark'?'white':'black'}}>
            <h1>Your text Summary</h1>
            <p><b>{text.split(" ").length}</b> words <b>{text.length}</b> characters</p>
            <p><b>{0.008 * text.split(" ").length} Minutes read</b></p>
            <h3>Your text Preview</h3>
            <p>{text.length>0?text:"Enter something in the textbox to preview here"}</p>
        </div>
        </>
    );
}
