
import './App.css';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
// import About from './components/About';
import React, { useState } from 'react';
import Alert from './components/Alert';

// import {
//   BrowserRouter as Router,
//   Switch,
//   Route,
//   Link
// } from "react-router-dom";

function App() {
  const [mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type)=>{
    setAlert({
        msg : message,
        type : type
    })
    setTimeout(() => {
      setAlert(null)
    }, 1500);
  }

  const toggleMode = ()=>{
    if(mode === 'light'){
      setMode('dark');
      document.body.style.backgroundColor = '#141442';
      showAlert("! Dark mode has been applied!!", "success");
    }
    else{
      setMode('light');
      document.body.style.backgroundColor = 'white';
      showAlert("! Light mode has been applied!!", "success");
    }
  }

  return (
    <>
    {/* <Router> */}
      <Navbar title="TextUtils" about="About TextUtils" mode={mode} toggleMode ={toggleMode}/>
      <Alert alert = {alert}/>
      {/* <Navbar /> */}
      <div className="container">
        {/* <Switch>
          <Route exact path="/about">    {/* exact means perfect match not a partial matching. Its a good method to practise*/}
            {/* <About mode={mode} toggleMode ={toggleMode}/>
          </Route>
          <Route exact path="/"> */} 
            <TextForm showAlert = {showAlert} heading="Enter the text to analyze" mode={mode} toggleMode ={toggleMode}/>
          {/* </Route>
        </Switch> */}
      </div>
      <br />
    {/* </Router> */}
    </>
  );
}

export default App;
